
Template.__checkName("comment_edit");
Template["comment_edit"] = new Template("Template.comment_edit", (function() {
  var view = this;
  return HTML.DIV({
    "class": "comment-edit"
  }, "\n    ", Blaze._TemplateWith(function() {
    return {
      check: Spacebars.call("edit"),
      doc: Spacebars.call(view.lookup("comment")),
      message: Spacebars.call("sorry_you_cannot_edit_this_comment")
    };
  }, function() {
    return Spacebars.include(view.lookupTemplate("checker"), function() {
      return [ "\n\n      ", HTML.DIV({
        "class": "grid grid-module"
      }, "\n        ", HTML.DIV({
        "class": "comment-form"
      }, "\n          ", Blaze._TemplateWith(function() {
        return {
          collection: Spacebars.call("Comments"),
          doc: Spacebars.call(view.lookup("comment")),
          id: Spacebars.call("editCommentForm"),
          template: Spacebars.call("bootstrap3-horizontal"),
          "label-class": Spacebars.call("control-label"),
          "input-col-class": Spacebars.call("controls"),
          type: Spacebars.call("method-update"),
          meteormethod: Spacebars.call("editComment"),
          fields: Spacebars.call(view.lookup("commentFields"))
        };
      }, function() {
        return Spacebars.include(view.lookupTemplate("quickForm"));
      }), "\n        "), "\n      "), "\n      \n      ", HTML.DIV({
        "class": "grid grid-module"
      }, "\n        ", HTML.A({
        "class": "delete-link",
        href: "/"
      }, Blaze.View("lookup:_", function() {
        return Spacebars.mustache(view.lookup("_"), "delete_comment");
      })), "\n      "), "\n\n    " ];
    });
  }), "\n  ");
}));
