
Template.__checkName("comment_submit");
Template["comment_submit"] = new Template("Template.comment_submit", (function() {
  var view = this;
  return Blaze.If(function() {
    return Spacebars.call(view.lookup("canComment"));
  }, function() {
    return [ "\n    ", HTML.DIV({
      "class": "comment-form comment-new module"
    }, "\n      ", HTML.H4({
      "class": "comment-new-heading"
    }, Blaze.View("lookup:_", function() {
      return Spacebars.mustache(view.lookup("_"), "add_comment");
    })), "\n      ", Blaze._TemplateWith(function() {
      return {
        collection: Spacebars.call("Comments"),
        id: Spacebars.call("submitCommentForm"),
        template: Spacebars.call("bootstrap3-horizontal"),
        "input-col-class": Spacebars.call("controls"),
        type: Spacebars.call("method"),
        meteormethod: Spacebars.call("submitComment"),
        fields: Spacebars.call(view.lookup("commentFields")),
        parentContext: Spacebars.call(view.lookup("."))
      };
    }, function() {
      return Spacebars.include(view.lookupTemplate("quickForm"));
    }), "\n    "), "\n  " ];
  }, function() {
    return [ "\n    ", Blaze.If(function() {
      return Spacebars.call(view.lookup("isLoggedIn"));
    }, function() {
      return [ "\n      ", HTML.P({
        "class": "cannot-comment-message"
      }, Blaze.View("lookup:_", function() {
        return Spacebars.mustache(view.lookup("_"), "sorry_you_do_not_have_the_rights_to_comments");
      })), "\n    " ];
    }, function() {
      return [ "\n      ", HTML.P({
        "class": "cannot-comment-message"
      }, HTML.A({
        href: function() {
          return Spacebars.mustache(view.lookup("pathFor"), Spacebars.kw({
            route: "signIn"
          }));
        }
      }, Blaze.View("lookup:_", function() {
        return Spacebars.mustache(view.lookup("_"), "please_log_in_to_comment");
      }))), "\n    " ];
    }), "\n  " ];
  });
}));
