
Template.__checkName("comment_controller");
Template["comment_controller"] = new Template("Template.comment_controller", (function() {
  var view = this;
  return Blaze._TemplateWith(function() {
    return {
      ready: Spacebars.call(view.templateInstance().subscriptionsReady())
    };
  }, function() {
    return Spacebars.include(view.lookupTemplate("loader"), function() {
      return [ "\n    ", Blaze._TemplateWith(function() {
        return {
          template: Spacebars.call(view.lookup("commentTemplate")),
          data: Spacebars.call(view.lookup("data"))
        };
      }, function() {
        return Spacebars.include(function() {
          return Spacebars.call(Template.__dynamic);
        });
      }), "\n  " ];
    });
  });
}));
