
Template.__checkName("comments_list_compact");
Template["comments_list_compact"] = new Template("Template.comments_list_compact", (function() {
  var view = this;
  return HTML.TABLE("\n    ", HTML.THEAD("\n      ", HTML.TR("\n        ", HTML.TD(Blaze.View("lookup:_", function() {
    return Spacebars.mustache(view.lookup("_"), "post");
  })), "\n        ", HTML.TD(Blaze.View("lookup:_", function() {
    return Spacebars.mustache(view.lookup("_"), "comment");
  })), "\n        ", HTML.TD(Blaze.View("lookup:fieldLabel", function() {
    return Spacebars.mustache(view.lookup("fieldLabel"));
  })), "\n      "), "\n    "), "\n    ", Blaze.Each(function() {
    return Spacebars.call(view.lookup("commentsCursor"));
  }, function() {
    return [ "\n      ", HTML.TR("\n        ", HTML.TD(HTML.A({
      href: function() {
        return [ Spacebars.mustache(view.lookup("pathFor"), "postPage", Spacebars.kw({
          _id: view.lookup("postId")
        })), "/" ];
      }
    }, Blaze.View("lookup:postTitle", function() {
      return Spacebars.mustache(view.lookup("postTitle"));
    }))), "\n        ", HTML.TD(Blaze.View("lookup:htmlBody", function() {
      return Spacebars.makeRaw(Spacebars.mustache(view.lookup("htmlBody")));
    })), "\n        ", HTML.TD(Blaze.View("lookup:fieldValue", function() {
      return Spacebars.mustache(view.lookup("fieldValue"));
    })), "\n      "), "\n    " ];
  }), "\n    ", Blaze.If(function() {
    return Spacebars.call(view.lookup("hasMoreComments"));
  }, function() {
    return [ "\n      ", HTML.TR("\n        ", HTML.TD({
      colspan: "2"
    }, "\n          ", HTML.A({
      "class": "more-button grid-module",
      href: "#"
    }, HTML.SPAN(Blaze.View("lookup:_", function() {
      return Spacebars.mustache(view.lookup("_"), "load_more");
    }))), "\n        "), "\n      "), "\n    " ];
  }), "\n  ");
}));
