
Template.__checkName("comment_list");
Template["comment_list"] = new Template("Template.comment_list", (function() {
  var view = this;
  return [ HTML.UL({
    "class": function() {
      return [ "comments comment-list ", Spacebars.mustache(view.lookup("commentListClass")) ];
    },
    "aria-live": "polite"
  }, "\n    ", Blaze.Each(function() {
    return Spacebars.call(view.lookup("childComments"));
  }, function() {
    return [ "\n      ", Spacebars.include(view.lookupTemplate("comment_item")), "\n    " ];
  }), "\n  "), "\n  ", Blaze._TemplateWith(function() {
    return {
      zone: Spacebars.call("commentThreadBottom"),
      moduleData: Spacebars.call(view.lookup("."))
    };
  }, function() {
    return Spacebars.include(view.lookupTemplate("modules"));
  }) ];
}));
